<template>
  <div id="app">
    <Nav v-on:register="showRegisterForm()" v-on:login="showLoginForm()"/>
    <router-view/>
    <div class="contact">
      <p>Contact Us</p>
      <i class="far fa-comment"></i>
    </div>
    <Register v-if="this.register == true" v-on:close="showRegisterForm()" v-on:member="changeForms()" />
    <Login v-if="this.login == true" v-on:close="showLoginForm()" v-on:not_member="changeForms()" />
  </div>
</template>

<script>
import Nav from "./components/Nav"
import Register from "./components/Register"
import Login from "./components/Login"

export default {
  data() {
    return {
      register: false,
      login: false
    }
  },
  components: {
    Nav,
    Register,
    Login
  },
  methods: {
    showRegisterForm() {
      this.register = !this.register;
    },
    showLoginForm() {
      this.login = !this.login;
    },
    changeForms() {
      this.login = !this.login;
      this.register = !this.register;
    }
  }
}
</script>


